<template>
  <div id="signup">
    <div class="signup-form">
      <h1>To-Do App</h1>
      <form @submit.prevent="onSubmit">
          <div class="input">
            <label for="name">Name</label>
            <p v-if="errors.name" class="alert alert-danger">{{ errors.name }}</p>
            <input
                type="text"
                id="name"
                v-model="name">
        </div>
        <div class="input">
          <label for="email">Mail</label>
          <p v-if="errors.email" class="alert alert-danger">{{ errors.email }}</p>
          <input
                type="email"
                id="email"
                v-model="email">
        </div>
        <div class="input">
          <label for="password">Password</label>
          <p v-if="errors.password" class="alert alert-danger">{{ errors.password }}</p>
          <input
                type="password"
                id="password"
                v-model="password">
        </div>
        <div class="submit">
          <button type="submit">Submit</button>
        </div>
      </form>
      <p style="text-align: center; padding-top: 10px">Already have an account ? <a href="/signin">Login</a></p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    data () {
      return {
        name: '',
        email: '',
        password: '',
        errors: {}
      }
    },
    methods: {
      onSubmit () {
        const formData = {
            name: this.name,
            email: this.email,
            password: this.password
        }
        console.log(formData)
        axios.post('/users/register', formData)
          .then(res => console.log(res))
          .catch(error => {
            this.errors = error.response.data
            console.log(error.response.data)
          })
      }
    }
  }
</script>

